.container {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
  }
  .counter {
    display: flex;
    align-items: center;
  }
  .name {
    font-size: 1.5rem;
    margin: 1rem 0 1rem 0;
  }
  .price {
    font-size: 1.5rem;
    margin: 1rem 0 1rem 0;
  }
  .discount {
    font-size: 1rem;
    font-style: italic;
    margin: 0 0 1rem 0;
  }
  .count {
    font-size: 2rem;
  }